header {
  color: #fff;
  position: fixed;
  top: 0;
  background-color: #200d43;
  width: 100%;
  padding: 1.5rem 0;
  z-index: 1;
  -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, 0.14), 0 2px 4px rgba(0, 0, 0, 0.28);
          box-shadow: 0 0 2px rgba(0, 0, 0, 0.14), 0 2px 4px rgba(0, 0, 0, 0.28); }
  header h1,
  header ul,
  header li,
  header a {
    color: inherit; }
  header h1 {
    display: inline-block;
    font-size: 1.6rem;
    font-family: 'Pacifico', cursive;
    margin: 0;
    font-weight: 100; }
  header a, header a:hover, header a:focus {
    text-decoration: none; }
  header ul {
    margin: 0;
    padding: .4rem 0;
    display: block;
    float: right; }
    header ul li {
      margin: 0 .3rem 0 0;
      padding: 0;
      display: inline-block;
      list-style: none; }
      header ul li:last-child {
        margin: 0; }
      header ul li.highlight a {
        background: -webkit-linear-gradient(50deg, #521cb3, #2f1361);
        background: -o-linear-gradient(50deg, #521cb3, #2f1361);
        background: linear-gradient(40deg, #521cb3, #2f1361);
        background: #521cb3;
        background: -webkit-linear-gradient(to bottom, #521cb3, #2f1361);
        background: -webkit-gradient(linear, left top, left bottom, from(#521cb3), to(#2f1361));
        background: -webkit-linear-gradient(top, #521cb3, #2f1361);
        background: -o-linear-gradient(top, #521cb3, #2f1361);
        background: linear-gradient(to bottom, #521cb3, #2f1361);
        margin-left: .75rem; }
        header ul li.highlight a:hover {
          background: -webkit-linear-gradient(50deg, #5c1dce, #321567);
          background: -o-linear-gradient(50deg, #5c1dce, #321567);
          background: linear-gradient(40deg, #5c1dce, #321567);
          background: #5c1dce;
          background: -webkit-linear-gradient(to bottom, #5c1dce, #321567);
          background: -webkit-gradient(linear, left top, left bottom, from(#5c1dce), to(#321567));
          background: -webkit-linear-gradient(top, #5c1dce, #321567);
          background: -o-linear-gradient(top, #5c1dce, #321567);
          background: linear-gradient(to bottom, #5c1dce, #321567); }
      header ul li a {
        font-size: .9rem;
        font-weight: 900;
        padding: .8rem 1rem;
        -webkit-transition: all .3s;
        -o-transition: all .3s;
        transition: all .3s;
        border-radius: .16rem; }
        header ul li a:hover {
          background-color: #3b1879; }

@media (min-width: 0) and (max-width: 968px) {
  header {
    height: auto; }
    header ul li {
      display: none; }
      header ul li.highlight {
        display: inline-block; } }



