section {
  background-color: #3b1879;
  padding: 3rem 0 5rem; }
  section .row .col {
    display: block;
    float: left;
    margin: 0;
    padding: 0;
    position: relative; }
  section .col-ad {
    width: 60%; }
    section .col-ad h1,
    section .col-ad p {
      color: rgba(255, 255, 255, 0.85); }
    section .col-ad h1 {
      font-weight: 900;
      font-size: 3.4rem;
      margin: 0 0 4rem; }
    section .col-ad p {
      font-weight: 100;
      font-size: 1.1rem;
      margin: 0 0 .8rem; }
      section .col-ad p.quote {
        padding: 0;
        color: rgba(255, 255, 255, 0.1);
        font-weight: 900;
        margin: 0;
        font-size: 10rem;
        line-height: 2rem;
        position: relative;
        top: .7rem;
        font-family: Roboto; }
      section .col-ad p.author {
        margin: 0;
        font-size: .9rem;
        text-align: right;
        color: rgba(255, 255, 255, 0.4); }
    section .col-ad .ad {
      width: 100%;
      padding: 4.5rem 7.74rem 0 0; }
  section .col-feature {
    width: 40%; }
    section .col-feature .feature {
      padding: 9rem 0 5rem 2rem;
      width: 100%;
      float: right; }

@media (min-width: 0) and (max-width: 968px) {
  section {
    height: auto; }
    section .row .col {
      width: 100%; }
      section .row .col:first-child {
        margin: 0 0 1rem; }
      section .row .col .feature {
        padding-top: 0;
        width: 100%; } }



